@-webkit-keyframes float {
  0%   {opacity: 0;}
  100% {opacity: 1;}
}

@keyframes float {
  0%   {opacity: 0;}
  100% {opacity: 1;}
}

.donatBlock {
  margin-bottom: 289px;
}

.title {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  margin: 0 auto;
  margin-bottom: 59px;
  margin-top: 59px;
  font-weight: 700;
  font-size: 23px;
  line-height: 27px;
  color: #0066ff;
  background: var(--background-modal);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.07);
  border-radius: 20px;
  padding: 19px 51px;
  transition:  0.3s;
  -webkit-animation-name: float;
          animation-name: float;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

.subTitle {
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: #000000;
  opacity: 0.5;
  margin-bottom: 23px;
}

.listBtn {
  display: flex;
  flex-direction: column;
  gap: 23px;
  width: 100%;
}

.bankCard {
  display: flex;
  background: #ffffff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.07);
  border-radius: 20px;
  padding: 16px 25px;
  gap: 17px;
  align-items: center;
  justify-content: flex-start;
  
}

.bankCardTitle {
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
}

.bankCardSubTitle {
  font-weight: 500;
  font-size: 13px;
  line-height: 15px;
  color: #000000;
  opacity: 0.3;
}

.bankCardImg {
  width: 58px;
  height: 58px;
  background: #FFF4EC;
}

.bankCardText {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 4px;
}

@media (max-width: 830px) {
  .donatBlock {
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media (max-width: 500px) {
  .donatBlock {
    margin-bottom: 224px;
  }

  .title {
    margin-top: 47px;
    margin-bottom: 47px;
    padding: 14.93px 39.16px;
    font-size: 18.0714px;
    line-height: 21px;
   }
}